      margin-top: 20px;
    }
  }
</style>
<script>
  const data = [{
    label: ' 1',
    children: [{
      label: ' 1-1',
      children: [{
        label: ' 1-1-1'
      }]
    }]
  }, {
    label: ' 2',
    children: [{
      label: ' 2-1',
      children: [{
        label: ' 2-1-1'
      }]
    }, {
      label: ' 2-2',
      children: [{
        label: ' 2-2-1'
      }]
    }]
  }, {
    label: ' 3',
    children: [{
      label: ' 3-1',
      children: [{
        label: ' 3-1-1'
      }]
    }, {
      label: ' 3-2',
      children: [{
        label: ' 3-2-1'
      }]
    }]
  }];

  const data2 = [{
    id: 1,
    label: ' 1',
    children: [{
      id: 4,
      label: ' 1-1',
      children: [{
        id: 9,
        label: ' 1-1-1'
      }, {
        id: 10,
        label: ' 1-1-2'
      }]
    }]
  }, {
    id: 2,
    label: ' 2',
    children: [{
      id: 5,
      label: ' 2-1'
    }, {
      id: 6,
      label: ' 2-2'
    }]
  }, {
    id: 3,
    label: ' 3',
    children: [{
      id: 7,
      label: ' 3-1'
    }, {
      id: 8,
      label: ' 3-2'
    }]
  }];

  const data3 = [{
    id: 1,
    label: ' 2',
    children: [{
      id: 3,
      label: ' 2-1',
      children: [{
        id: 4,
        label: ' 3-1-1'
      }, {
        id: 5,
        label: ' 3-1-2',
        disabled: true
      }]
    }, {
      id: 2,
      label: ' 2-2',
      disabled: true,
      children: [{
        id: 6,
        label: ' 3-2-1'
      }, {
        id: 7,
        label: ' 3-2-2',
        disabled: true
      }]
    }]
  }];

  let id = 1000;

  const regions = [{
    'name': 'region1'
  }, {
    'name': 'region2'
  }];

  let count = 1;

  const props = {
    label: 'name',
    children: 'zones'
  };

  const props1 = {
    label: 'name',
    children: 'zones',
    isLeaf: 'leaf'
  };

  const defaultProps = {
    children: 'children',
    label: 'label'
  };

  export default {
    watch: {
      filterText(val) {
        this.$refs.tree2.filter(val);
      }
    },

    methods: {
      handleCheckChange(data, checked, indeterminate) {
        console.log(data, checked, indeterminate);
      },
      handleNodeClick(data) {
        console.log(data);
      },
      loadNode(node, resolve) {
        if (node.level === 0) {
          return resolve([{ name: 'region1' }, { name: 'region2' }]);
        }
        if (node.level > 3) return resolve([]);
        var hasChild;
        if (node.data.name === 'region1') {
          hasChild = true;
        } else if (node.data.name === 'region2') {
          hasChild = false;          
        } else {
          hasChild = Math.random() > 0.5;
        }

        setTimeout(function() {
          var data;
          if (hasChild) {
            data = [{
              name: 'zone' + count++
            }, {
              name: 'zone' + count++
            }];
          } else {
            data = [];
          }

          resolve(data);
        }, 500);
      },
      loadNode1(node, resolve) {
        if (node.level === 0) {
          return resolve([{ name: 'region' }]);
        }
        if (node.level > 1) return resolve([]);

        setTimeout(() => {
          const data = [{
            name: 'leaf',
            leaf: true
          }, {
            name: 'zone'
          }];

          resolve(data);
        }, 500);
      },
      getCheckedNodes() {
        console.log(this.$refs.tree.getCheckedNodes());
      },
      getCheckedKeys() {
        console.log(this.$refs.tree.getCheckedKeys());
      },
      setCheckedNodes() {
        this.$refs.tree.setCheckedNodes([
          {
            id: 5,
            label: ' 2-1'
          },
          {
            id: 9,
            label: ' 1-1-1'
          }
        ]);
      },
      setCheckedKeys() {
        this.$refs.tree.setCheckedKeys([3]);
      },
      resetChecked() {
        this.$refs.tree.setCheckedKeys([]);
      },
      append(data) {
        const newChild = { id: id++, label: 'testtest', children: [] };
        if (!data.children) {
          this.$set(data, 'children', []);
        }
        data.children.push(newChild);
      },

      remove(node, data) {
        const parent = node.parent;
        const children = parent.data.children || parent.data;
        const index = children.findIndex(d => d.id === data.id);
        children.splice(index, 1);
      },

      renderContent(h, { node, data }) {
        return (
        <span>
          <span class="t-tree-node__label">{node.label}</span>
          <span style="float: right;">
            <button class="btn btn-link" style="padding: 4px; font-size: 12px;" type="text" on-click={() => this.append(data)}></button>
            <button class="btn btn-link" style="padding: 4px; font-size: 12px;" type="text" on-click={() => this.remove(node, data)}></button>
          </span>
        </span>);
      },

      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      }
    },

    data() {
      return {
        data,
        data2,
        data3,
        data4: JSON.parse(JSON.stringify(data2)),
        regions,
        defaultProps,
        props,
        props1,
        defaultCheckedKeys: [5],
        defaultExpandedKeys: [2, 3],
        filterText: '',
        userDefinedData: [{
          id: 1,
          label: ' 1',
          children: [{
            id: 4,
            label: ' 1-1',
            children: [{
              id: 9,
              label: ' 1-1-1'
            }, {
              id: 10,
              label: ' 1-1-2'
            }]
          }]
        }, {
          id: 2,
          label: ' 2',
          children: [{
            id: 5,
            label: ' 2-1'
          }, {
            id: 6,
            label: ' 2-2'
          }]
        }, {
          id: 3,
          label: ' 3',
          children: [{
            id: 7,
            label: ' 3-1'
          }, {
            id: 8,
            label: ' 3-2'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
      };
    }
  };
</script>


