<script>
  module.exports = {
    data () {
      return {
        show: false
      }
    },
    methods: {
      open() {
        this.$alert('');
      },

      open2() {
        this.$alert({
          message: '',
          type: 'success'
        });
      },

      open3() {
        this.$alert({
          message: '',
          type: 'warning'
        });
      },

      open4() {
        this.$alert({
          showClose: true,
          message: '',
          type: 'danger'
        });
      },

      open5() {
        this.$alert({
          type: 'success',
          message: '',
          duration: 3000,
          closable: false
        });
      },

      open6() {
        this.$alert({
          showClose: true,
          message: '',
          type: 'success'
        });
      },

      open7() {
        this.$alert({
          showClose: true,
          message: '',
          type: 'warning'
        });
      },

      open8() {
        this.$alert({
          showClose: true,
          message: '',
          type: 'danger'
        });
      },

      openCenter() {
        this.$alert({
          message: '',
          center: true
        });
      },

      openHTML() {
        this.$alert({
          dangerouslyUseHTMLString: true,
          message: '<strong> <i>HTML</i> </strong>'
        });
      }
    }
  };
</script>


